<template>
  <v-app>
    <project-nav-drawer
      :drawer="drawer"
      :project="project"
      @update="closeDrawer"
    ></project-nav-drawer>
    <project-header
      :drawer="drawer"
      :project="project"
      :currentMember="currentMember"
      @update="openDrawer"
    />
    <v-main>
      <router-view v-if="isLoaded" :project="project"></router-view>
    </v-main>
    <v-footer app fixed>
      <v-spacer></v-spacer>
      &copy; {{ new Date().getFullYear() }} DoTS. All rights reserved.
    </v-footer>
  </v-app>
</template>

<script>
import ProjectHeader from '@/components/ProjectHeader.vue'
import ProjectNavDrawer from '@/components/ProjectNavDrawer.vue'

export default {
  components: {
    ProjectHeader,
    ProjectNavDrawer,
  },
  data: () => ({
    drawer: false,
    isLoaded: false,
    project: {},
    currentMember: '',
  }),
  async created() {
    try {
      const result = await this.$http.get('/project', {
        params: {
          id: this.$route.params.id,
        },
      })
      this.project = result.data
      this.isLoaded = true
      this.$store.commit('sendProjectInfo', {
        currProject: this.project.project_name,
      })
    } catch (e) {
      console.error(e)
      this.$router.push({ name: 'error' })
    }
  },
  mounted() {
    this.EventBus.$on('sendMemberName', payload => {
      if (!payload.memberFirstName && !payload.memberLastName) {
        this.currentMember = ''
      } else {
        this.currentMember =
          payload.memberFirstName + ' ' + payload.memberLastName
      }
    })
  },
  methods: {
    openDrawer() {
      this.drawer = true
    },
    closeDrawer() {
      this.drawer = false
    },
  },
}
</script>
