<template>
  <v-container grid-list-xl grid-list-lg class="content-index">
    <div>
      <span class="text-h4 font-weight-bold">새 프로젝트 생성</span>
      <br />
      <span class="text-h4 font-weight-light"
        >새 프로젝트의 정보를 입력하세요.</span
      >
    </div>
    <br />
    <v-layout>
      <v-flex xl6 lg9 sm12>
        <validation-observer v-slot="{ handleSubmit, reset }">
          <v-form
            @submit.prevent="handleSubmit(onSubmit)"
            @reset.prevent="reset"
          >
            <div class="text-h5">프로젝트 이름</div>
            <div class="text-h6 mb-2">새 프로젝트를 대표하는 이름입니다.</div>
            <validation-provider
              name="projectName"
              :rules="{ required: true, min: { length: 4 } }"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.projectName"
                :error-messages="errors[0]"
                required
                single-line
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <div class="text-h5">프로젝트 설명</div>
            <div class="text-h6 mb-2">
              프로젝트에 대한 설명을 한 문장으로 요약해주세요.
            </div>
            <validation-provider
              name="description"
              :rules="{ max: { length: 30 } }"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.description"
                :error-messages="errors[0]"
                counter="30"
                maxlength="30"
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <div class="text-h5">프로젝트 주제</div>
            <div class="text-h6 mb-2">
              프로젝트의 핵심 주제로, 루트 노드(레벨 0)의 이름이기도 합니다.
            </div>
            <validation-provider
              name="topic"
              rules="required"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.topic"
                :error-messages="errors[0]"
                required
                single-line
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <div class="text-h5">팀 이름</div>
            <div class="text-h6 mb-2">
              프로젝트를 수행하는 팀의 이름입니다. 팀원은 나중에 추가할 수
              있습니다.
            </div>
            <validation-provider
              name="teamName"
              rules="required"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.teamName"
                :error-messages="errors[0]"
                required
                single-line
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <v-layout class="text-center">
              <v-btn type="reset" color="info" tile @click="onClear"
                >초기화</v-btn
              >
              <v-spacer></v-spacer>
              <v-btn type="submit" color="success" tile>프로젝트 생성</v-btn>
            </v-layout>
          </v-form>
        </validation-observer>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {
  ValidationObserver,
  ValidationProvider,
  extend,
  localize,
} from 'vee-validate'
import { required, min, max } from 'vee-validate/dist/rules'

export default {
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  props: {
    user: {
      type: Object,
    },
  },
  data() {
    return {
      form: {
        projectName: '',
        description: '',
        topic: '',
        teamName: '',
        user: this.user,
      },
      message: {
        required: '{_field_} 항목을 입력해주세요.',
        min: '{_field_} 항목은 최소 {length}글자 이상 입력해주세요.',
        max: '{_field_} 항목은 최대 {length}글자까지 입력해주세요.',
      },
    }
  },
  mounted() {
    localize({
      ko: {
        names: {
          projectName: '프로젝트 이름',
          description: '프로젝트 설명',
          topic: '프로젝트 주제',
          teamName: '팀 이름',
        },
        messages: {
          required: this.message.required,
          min: this.message.min,
          max: this.message.max,
        },
      },
    })
    localize('ko')
    extend('required', required)
    extend('min', min)
    extend('max', max)
  },
  methods: {
    async onSubmit() {
      try {
        const result = await this.$http.post('/project', this.form)
        if (!result) {
          throw new Error('서버에서 요청을 거부했습니다.')
        }
        alert('프로젝트 생성이 완료되었습니다.')
        this.$router.push({ name: 'user' })
      } catch (e) {
        alert(e.response.data.message)
      }
    },
    onClear() {
      this.form.projectName = ''
      this.form.description = ''
      this.form.topic = ''
      this.form.teamName = ''
    },
  },
}
</script>
