<template>
  <v-container fill-height fluid class="pa-0">
    <v-layout justify-center align-center>
      <v-flex xl3 lg3 sm5>
        <validation-observer v-slot="{ handleSubmit, reset }">
          <v-form
            @submit.prevent="handleSubmit(onSubmit)"
            @reset.prevent="reset"
          >
            <validation-provider
              name="email"
              rules="required|email"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.email"
                label="이메일"
                :error-messages="errors[0]"
                required
                single-line
                solo
              ></v-text-field>
            </validation-provider>
            <validation-provider
              name="password"
              :rules="{ required: true, min: { length: 6 } }"
              v-slot="{ errors }"
              vid="password"
            >
              <v-text-field
                v-model="form.password"
                label="비밀번호"
                :error-messages="errors[0]"
                required
                single-line
                solo
                :append-icon="visibility1 ? 'mdi-eye' : 'mdi-eye-off'"
                :type="visibility1 ? 'text' : 'password'"
                @click:append="visibility1 = !visibility1"
              ></v-text-field>
            </validation-provider>
            <validation-provider
              name="passwordConfirm"
              :rules="{ required: true, confirmed: { target: 'password' } }"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.passwordConfirm"
                label="비밀번호 확인"
                :error-messages="errors[0]"
                required
                single-line
                solo
                :append-icon="visibility2 ? 'mdi-eye' : 'mdi-eye-off'"
                :type="visibility2 ? 'text' : 'password'"
                @click:append="visibility2 = !visibility2"
              ></v-text-field>
            </validation-provider>
            <validation-provider
              name="lastName"
              rules="required"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.lastName"
                label="성"
                :error-messages="errors[0]"
                required
                single-line
                solo
              ></v-text-field>
            </validation-provider>
            <validation-provider
              name="firstName"
              rules="required"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.firstName"
                label="이름"
                :error-messages="errors[0]"
                required
                single-line
                solo
              ></v-text-field>
            </validation-provider>
            <v-layout class="text-center">
              <v-btn type="reset" color="info" tile @click="onClear"
                >초기화</v-btn
              >
              <v-spacer></v-spacer>
              <v-btn type="submit" color="success" tile>회원가입</v-btn>
            </v-layout>
          </v-form>
        </validation-observer>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {
  ValidationObserver,
  ValidationProvider,
  extend,
  localize,
} from 'vee-validate'
import { required, email, min, confirmed } from 'vee-validate/dist/rules'
import randomColor from 'randomcolor'

export default {
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  data() {
    return {
      form: {
        email: '',
        password: '',
        passwordConfirm: '',
        firstName: '',
        lastName: '',
        color: '',
      },
      message: {
        required: '{_field_} 항목을 입력해주세요.',
        email: '올바른 이메일 형식으로 입력해주세요.',
        min: '{_field_} 항목은 최소 {length}글자 이상 입력해주세요.',
        confirmed: '비밀번호가 일치하지 않습니다.',
      },
      visibility1: false,
      visibility2: false,
    }
  },
  mounted() {
    localize({
      ko: {
        names: {
          email: '이메일',
          password: '비밀번호',
          passwordConfirm: '비밀번호 확인',
          firstName: '이름',
          lastName: '성',
        },
        messages: {
          required: this.message.required,
          email: this.message.email,
          min: this.message.min,
          confirmed: this.message.confirmed,
        },
      },
    })
    localize('ko')
    extend('required', required)
    extend('email', email)
    extend('min', min)
    extend('confirmed', confirmed)
  },
  methods: {
    async onSubmit() {
      this.getPersonalColor()
      try {
        const result = await this.$http.post('/user', this.form)
        if (!result) {
          throw new Error('서버에서 요청을 거부했습니다.')
        }
        alert('회원가입이 완료되었습니다. 로그인 화면으로 이동합니다.')
        this.$router.push({ name: 'login' })
      } catch (e) {
        alert(e.response.data.message)
      }
    },
    onClear() {
      this.form.email = ''
      this.form.password = ''
      this.form.passwordConfirm = ''
      this.form.firstName = ''
      this.form.lastName = ''
      this.visibility1 = false
      this.visibility2 = false
    },
    getPersonalColor() {
      this.form.color = randomColor({
        luminosity: 'bright',
      })
    },
  },
}
</script>
