<template>
  <v-app>
    <user-header :user="user" :items="items" />
    <v-main>
      <br />
      <v-layout justify-center>
        <v-flex xl10 lg10 sm10>
          <router-view :user="user"></router-view>
        </v-flex>
      </v-layout>
    </v-main>
    <v-footer> </v-footer>
  </v-app>
</template>

<script>
import jwtDecode from 'jwt-decode'
import UserHeader from '@/components/UserHeader.vue'

export default {
  components: {
    UserHeader,
  },
  data() {
    return {
      user: {
        firstName: '',
        lastName: '',
        email: '',
        color: '',
      },
      items: [
        {
          title: '계정 관리',
          action: '',
          disabled: true,
        },
        {
          title: '로그아웃',
          action: this.signout,
          disabled: false,
        },
      ],
    }
  },
  created() {
    const token = localStorage.getItem('userToken')
    if (!token) this.signout()

    const decoded = jwtDecode(token)

    this.user.firstName = decoded.first_name
    this.user.lastName = decoded.last_name
    this.user.email = decoded.email
    this.user.color = decoded.color
  },
  methods: {
    signout() {
      this.$store.commit('deleteToken')
      this.$router.push({ name: 'index' })
    },
  },
}
</script>
