<template>
  <v-bottom-sheet v-model="isSheetOpened" inset>
    <v-sheet class="pa-6" tile color="blue darken-4">
      <div class="text-h4 white--text">{{ pinnedNode.name.host }}</div>
      <div class="text-h5 grey--text text--lighten-1">
        {{ pinnedNode.name.rest }}
      </div>
    </v-sheet>
    <v-sheet class="pa-6" tile color="blue darken-3">
      <div class="text-h6 font-weight-light grey--text text--lighten-2">
        {{ pinnedNode.url }}
      </div>
    </v-sheet>
    <v-sheet class="pa-6" tile color="blue lighten-2">
      <v-row no-gutters>
        <v-col cols="3">
          <v-sheet tile color="blue lighten-2" class="text-h5">
            <span class="font-weight-light">노드 ID:</span>
            <span class="font-weight-medium ml-2">{{ pinnedNode.id }}</span>
          </v-sheet>
        </v-col>
        <v-col cols="3">
          <v-sheet tile color="blue lighten-2" class="text-h5">
            <span class="font-weight-light">노드 레벨:</span>
            <span class="font-weight-medium ml-2">{{ pinnedNode.level }}</span>
          </v-sheet>
        </v-col>
        <v-col cols="6">
          <v-sheet
            v-if="pinnedNode.tagged"
            tile
            color="blue lighten-2"
            class="text-h5"
          >
            <span class="font-weight-light">태그 표시 여부:</span>
            <span
              v-if="pinnedNode.tagged[0] === 'True'"
              class="font-weight-bold ml-2 green--text text--darken-2"
              >TRUE</span
            >
            <span v-else class="font-weight-bold ml-2 red--text text--darken-3"
              >FALSE</span
            >
          </v-sheet>
          <!-- <v-sheet v-if="pinnedNode.tagged.length > 0" tile color="blue lighten-2" class="text-h5">
                <span class="font-weight-light">태그 표시한 팀원:</span>
                <span
                  v-for="member in project.members"
                  :key="member.email"
                  class="font-weight-medium ml-2"
                >
                  <span v-for="(user, index) in pinnedNode.tagged" :key="index" class="mx-2">
                    <v-avatar v-if="member.email === user" :color="member.color" size="32">
                      <span class="white--text text-subtitle-2">{{ member.last_name.charAt(0) }}</span>
                    </v-avatar>
                  </span>
                </span>
              </v-sheet>
              <v-sheet v-else tile color="blue lighten-2" class="text-h5">
                <span class="font-weight-light">태그 표시한 팀원:</span>
                <span class="font-weight-medium ml-2">없음</span>
              </v-sheet> -->
        </v-col>
      </v-row>
    </v-sheet>
    <v-sheet v-if="pinnedNode.id !== 1" class="pa-6" tile>
      <div class="text-h5 font-weight-medium">
        <span v-if="pinnedNode.level > 1">{{ pinnedNode.keyword.main }}</span>
        <span v-else>연관 검색어</span>
      </div>
      <div class="text-h6 font-weight-light">
        <div v-if="pinnedNode.level > 1" class="ma-0">
          <span
            v-for="(subKeyword, index) in pinnedNode.keyword.sub"
            :key="index"
            >{{ subKeyword
            }}<span v-if="index !== pinnedNode.keyword.sub.length - 1"
              >,
            </span></span
          >
        </div>
        <div v-else class="ma-0">
          <span
            v-for="(keyword, index) in pinnedNode.relativeKeywordList"
            :key="index"
            >{{ keyword
            }}<span v-if="index !== pinnedNode.relativeKeywordList.length - 1"
              >,
            </span></span
          >
        </div>
      </div>
    </v-sheet>
    <v-sheet
      v-if="pinnedNode.level > 1"
      class="pa-6"
      tile
      dark
      color="grey darken-2"
    >
      <div
        v-for="(content, index) in pinnedNode.pageContents"
        :key="index"
        class="text-subtitle-2 font-weight-light"
      >
        {{ content.length > 50 ? content.substr(0, 49).concat('...') : content
        }}<br />
      </div>
    </v-sheet>
    <v-sheet
      v-if="pinnedNode.memo !== ''"
      class="pa-6"
      tile
      dark
      color="grey darken-3"
    >
      <span class="text-h5 font-weight-medium">MEMO</span>
      <br />
      <span class="text-h6 font-weight-light">{{ pinnedNode.memo }}</span>
    </v-sheet>
  </v-bottom-sheet>
</template>

<script>
export default {
  name: 'node-detail-sheet',
  props: {
    pinnedNode: {
      type: Object,
    },
    project: {
      type: Object,
    },
    sheet: {
      type: Boolean,
    },
  },
  data() {
    return {
      isSheetOpened: this.sheet,
    }
  },
  watch: {
    sheet(val) {
      this.isSheetOpened = val
    },
    isSheetOpened(val) {
      if (!val) {
        this.$emit('update')
      }
    },
  },
}
</script>
