import axios from 'axios'

const apiRootPath =
  process.env.NODE_ENV !== 'production' ? 'http://localhost:3000/api' : '/api'

// 토큰의 유효성을 검사하기 위해 토큰을 헤더에 담아 서버로 전달
export const pageCheck = async (to, from, next) => {
  // 크롬 확장프로그램 ID
  // const extensionId =
  //   process.env.NODE_ENV !== 'production' ? 'jpiagnpijljkijffgaidojpebhmijljc' : 'kbbjkdflgdjckldapfdldgeedcmhmglk'
  const extensionId = localStorage.getItem('extensionId')

  try {
    await axios.post(`${apiRootPath}/user/check`, {
      headers: {
        Authorization: localStorage.getItem('userToken'),
      },
    })

    // 크롬 확장 프로그램에 토큰 전달
    const token = localStorage.getItem('userToken')
    // eslint-disable-next-line no-undef
    chrome.runtime.sendMessage(extensionId, { userToken: token }, response => {
      // eslint-disable-next-line no-undef
      if (chrome.runtime.lastError) {
        next('/need-extension')
      } else {
        console.log(response.result)

        // 확장 프로그램 설치 페이지에서 사용자 메인 메뉴로 이동
        if (location.href.indexOf('/need-extension') !== -1) {
          next('/p')
        }
      }
    })

    next()
  } catch (e) {
    const errorMessage = e.response.data.message
    if (errorMessage === 'invalid token') {
      alert('유효하지 않은 토큰입니다. 다시 로그인해주세요.')
    } else if (errorMessage === 'jwt expired') {
      alert('로그인 시간이 만료되었습니다. 다시 로그인해주세요.')
    } else {
      alert('오류가 발생하였습니다. 다시 로그인해주세요.')
    }
    localStorage.removeItem('userToken')
    // eslint-disable-next-line
    chrome.runtime.sendMessage(
      extensionId,
      { userToken: 'logout' },
      response => {
        console.log(response.result)
      },
    )
    next('/')
  }
}

// 로컬 스토리지에 토큰이 존재하는지 검사
export const tokenCheck = (to, from, next) => {
  if (localStorage.getItem('userToken')) return next('/p')
  next()
}
