import Vue from 'vue'
import VueRouter from 'vue-router'

import { pageCheck, tokenCheck } from '@/lib/routerAuth'

import ErrorPage from '@/views/ErrorPage.vue'
import LandingContainer from '@/views/landing/Index.vue'
import IndexPage from '@/views/landing/IndexPage.vue'
import AboutPage from '@/views/landing/AboutPage.vue'
import FeaturesPage from '@/views/landing/FeaturesPage.vue'
import PricingPage from '@/views/landing/PricingPage.vue'
import HelpPage from '@/views/landing/HelpPage.vue'
import AuthContainer from '@/views/auth/Index.vue'
import LoginPage from '@/views/auth/LoginPage.vue'
import RegisterPage from '@/views/auth/RegisterPage.vue'
import UserContainer from '@/views/user/Index.vue'
import UserPage from '@/views/user/UserPage.vue'
import NeedExtensionPage from '@/views/user/NeedExtensionPage.vue'
import CreateProjectPage from '@/views/user/CreateProjectPage.vue'
import ProjectContainer from '@/views/project/Index.vue'
import PrintNodePage from '@/views/project/PrintNodePage.vue'
import ProjectInviteAuth from '@/views/project/ProjectInviteAuth.vue'

Vue.use(VueRouter)

const routes = [
  {
    // 랜딩 페이지 및 메뉴
    path: '/',
    component: LandingContainer,
    beforeEnter: tokenCheck,
    children: [
      {
        path: '',
        name: 'index',
        component: IndexPage,
      },
      {
        path: '/about',
        name: 'about',
        component: AboutPage,
      },
      {
        path: '/features',
        name: 'features',
        component: FeaturesPage,
      },
      {
        path: '/pricing',
        name: 'pricing',
        component: PricingPage,
      },
      {
        path: '/help',
        name: 'help',
        component: HelpPage,
      },
    ],
  },
  {
    // 로그인 및 회원가입 페이지
    path: '/',
    component: AuthContainer,
    beforeEnter: tokenCheck,
    children: [
      {
        path: '/login',
        name: 'login',
        component: LoginPage,
      },
      {
        path: '/register',
        name: 'register',
        component: RegisterPage,
      },
    ],
  },
  {
    // 프로젝트
    // '/:id': 해당 id를 가진 프로젝트
    path: '/project/:id',
    component: ProjectContainer,
    beforeEnter: pageCheck,
    children: [
      {
        path: '',
        name: 'project',
        component: PrintNodePage,
      },
    ],
  },
  {
    // 사용자 메인 메뉴
    path: '/p',
    component: UserContainer,
    beforeEnter: pageCheck,
    children: [
      {
        path: '',
        name: 'user',
        component: UserPage,
      },
      {
        path: '/p/create-project',
        name: 'create-project',
        component: CreateProjectPage,
      },
      {
        path: '/need-extension',
        name: 'need-extension',
        component: NeedExtensionPage,
      },
    ],
  },
  {
    path: '/auth',
    component: ProjectInviteAuth,
  },
  {
    // 그 외의 경로로 접근할 경우 에러 페이지로 리다이렉트
    path: '*',
    redirect: '/error',
  },
  {
    // 404 에러 페이지
    path: '/error',
    name: 'error',
    component: ErrorPage,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
